<template>
    <p-card>
        <template #content>
            <div>
                <p-toolbar class="p-p-1">
                    <template #left>
                        <div class="p-grid p-ml-2">
                            <div class="p-row">
                                <label
                                    ><strong
                                        >Screening
                                        {{
                                            demographic.sex == null
                                                ? ""
                                                : demographic.sex == "M"
                                                ? "Male"
                                                : demographic.sex == "F"
                                                ? "Female"
                                                : " Gender: " + demographic.sex
                                        }}
                                    </strong></label
                                ><br />
                                <label
                                    ><i
                                        v-if="demographic.chart != null"
                                        style="cursor: pointer;"
                                        @click="
                                            redirectToPartner(demographic.chart)
                                        "
                                    >
                                        {{ type }} :
                                        {{ demographic.firstName }}
                                        {{ demographic.lastName }}
                                    </i>
                                    <i v-else>No {{ type }}</i>
                                </label>
                            </div>
                        </div>
                    </template>
                    <template #right>
                        <p-button
                            label="Save"
                            class="p-mr-2 app-button p-button-sm"
                            @click="saveLabTest"
                        />
                    </template>
                </p-toolbar>

                <p-data-table
                    :value="demographic.lab_test"
                    editMode="cell"
                    showGridlines
                    class="p-datatable-sm p-mt-1 p-mb-2"
                    responsiveLayout="scroll"
                    :scrollable="true"
                    style="min-height: 200px;"
                    scrollHeight="200px"
                >
                    <p-column key="title" field="title">
                        <template #header>
                            <strong>Date</strong>
                        </template>

                        <template #body="{ data }">
                            <strong>{{ data.title }}</strong>
                        </template>
                    </p-column>

                    <p-column key="p1" field="p1">
                        <template #header>
                            <p-calendar
                                v-model="date1"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <!-- <strong v-if="data.title == 'Date' && data.p1">
                                {{ formatDate(data.p1) }}
                            </strong> -->

                            <span> {{ data.p1 ? data.p1 : "-" }}</span>
                        </template>

                        <template #editor="{ data }">
                            <!-- <p-calendar
                                v-model="data.p1"
                                v-if="data.title == 'Date'"
                                date-format="mm/dd/yy"
                            /> -->
                            <p-dropdown
                                v-model="data.p1"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>

                    <p-column key="p2" field="p2">
                        <template #header>
                            <p-calendar
                                v-model="date2"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <span> {{ data.p2 ? data.p2 : "-" }}</span>
                        </template>
                        <template #editor="{ data }">
                            <p-dropdown
                                v-model="data.p2"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>

                    <p-column key="p3" field="p3">
                        <template #header>
                            <p-calendar
                                v-model="date3"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <span> {{ data.p3 ? data.p3 : "-" }}</span>
                        </template>
                        <template #editor="{ data }">
                            <p-dropdown
                                v-model="data.p3"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>

                    <p-column key="p4" field="p4">
                        <template #header>
                            <p-calendar
                                v-model="date4"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <span> {{ data.p4 ? data.p4 : "-" }}</span>
                        </template>
                        <template #editor="{ data }">
                            <p-dropdown
                                v-model="data.p4"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>

                    <p-column key="p5" field="p5">
                        <template #header>
                            <p-calendar
                                v-model="date5"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <span> {{ data.p5 ? data.p5 : "-" }}</span>
                        </template>
                        <template #editor="{ data }">
                            <p-dropdown
                                v-model="data.p5"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>

                    <p-column key="p6" field="p6">
                        <template #header>
                            <p-calendar
                                v-model="date6"
                                date-format="mm/dd/yy"
                                style="width: 80px !important;"
                        /></template>
                        <template #body="{ data }">
                            <span> {{ data.p6 ? data.p6 : "-" }}</span>
                        </template>
                        <template #editor="{ data }">
                            <p-dropdown
                                v-model="data.p6"
                                :options="testOptions"
                                optionLabel="label"
                                optionValue="value"
                            />
                        </template>
                    </p-column>
                </p-data-table>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, ref } from "vue";
import { useStore } from "vuex";
import { toRefs } from "vue-demi";
import format from "date-fns/format";
import { DATE_FORMAT_STRING, DATE_FORMAT_STRING_US } from "@/config";
import { $axios } from "@/service/app-http";

import { useToast } from "primevue/usetoast";
import { TIME_0 } from "@/constants";
import { watch } from "vue-demi";
import { useRouter } from "vue-router";

export default {
    props: {
        type: String
    },
    setup(props) {
        const store = useStore();
        const toast = useToast();
        const { type } = toRefs(props);
        const router = useRouter();

        const date1 = ref(null);
        const date2 = ref(null);
        const date3 = ref(null);
        const date4 = ref(null);
        const date5 = ref(null);
        const date6 = ref(null);

        const demographic = computed(() => {
            let patient = null;
            if (type.value == "Patient") patient = store.state.artCycle.patient;
            else patient = store.state.artCycle.partner;

            if (patient != null) {
                date1.value = patient.lab_date.date1
                    ? new Date(patient.lab_date.date1 + TIME_0)
                    : null;

                date2.value = patient.lab_date.date2
                    ? new Date(patient.lab_date.date2 + TIME_0)
                    : null;

                date3.value = patient.lab_date.date3
                    ? new Date(patient.lab_date.date3 + TIME_0)
                    : null;

                date4.value = patient.lab_date.date4
                    ? new Date(patient.lab_date.date4 + TIME_0)
                    : null;

                date5.value = patient.lab_date.date5
                    ? new Date(patient.lab_date.date5 + TIME_0)
                    : null;

                date6.value = patient.lab_date.date6
                    ? new Date(patient.lab_date.date6 + TIME_0)
                    : null;
            }

            return patient;
        });

        const testOptions = [
            {
                label: "-",
                value: null
            },
            {
                label: "pos",
                value: "pos"
            },
            {
                label: "neg",
                value: "neg"
            }
        ];

        watch(demographic.value, (newValue) => {
            console.log("he");
            console.log(newValue.lab_date);
            date1.value =
                newValue == null || newValue.lab_date.date1 == null
                    ? null
                    : new Date(newValue.lab_date.date1 + TIME_0);
        });

        const formatDate = (date) => {
            return format(date, DATE_FORMAT_STRING_US);
        };

        // const date1 = computed(() => {
        //     return demographic == null ||
        //         demographic.value.lab_date.date1 == null
        //         ? null
        //         : new Date(demographic.value.lab_date.date1 + TIME_0);
        // });

        const pFormat = (date) => {
            if (date == null) return null;
            return format(date, DATE_FORMAT_STRING);
        };

        const saveLabTest = () => {
            console.log(demographic);
            if (demographic.value != null && demographic.value.chart != null) {
                const fDates = {
                    date1: pFormat(date1.value),
                    date2: pFormat(date2.value),
                    date3: pFormat(date3.value),
                    date4: pFormat(date4.value),
                    date5: pFormat(date5.value),
                    date6: pFormat(date6.value)
                };

                const payload = {
                    patientId: demographic.value.chart,
                    labTestData: demographic.value.lab_test,
                    labDate: fDates
                };

                store.commit("artCycle/SET_UPDATE_LABTEST_ONSTORAGE", {
                    patientId: demographic.value.chart,
                    labTest: demographic.value.lab_test,
                    labDate: fDates
                });

                $axios
                    .post("lab-test/save", payload)
                    .then((resp) => {
                        toast.add({
                            severity: "success",
                            summary: "Success",
                            detail: "Saved successfully",
                            life: 3000
                        });
                    })
                    .catch((err) => {});
            }
        };

        // const labTestTable = ref([
        //     {
        //         title: "Date",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "anti-HCV",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "anti-HBs",
        //         p1: "pos",
        //         p2: "neg",
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "anti-HIV1 + HIV2",
        //         p1: "pos",
        //         p2: "neg",
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "HBsAG",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "Hepatitis B",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "Hepatitis C",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "HIV Ab (Westernblot)",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     },
        //     {
        //         title: "HIV Ab (Westernblot)",
        //         p1: null,
        //         p2: null,
        //         p3: null,
        //         p4: null,
        //         p5: null,
        //         p6: null
        //     }
        // ]);

        return {
            date1,
            date2,
            date3,
            date4,
            date5,
            date6,
            //    labTestTable,
            testOptions,
            formatDate,
            demographic,
            saveLabTest,
            redirectToPartner: (patientId) => {
                if (type.value == "Patient") return;
                let queryParam = {
                    patientId: patientId,
                    to: "art-cycle"
                };

                router.push({
                    name: "redirect",
                    query: queryParam
                }); // zubaer added this
            }
        };
    }
};
</script>

<style scoped>
h3 {
    margin-top: 0;
    margin-bottom: 0.5rem;
}

.p-calendar {
    height: 24.5px;
}
</style>
